    text-align: center;
}
.el-tree-node, .el-tree-node__children {
    position: relative;
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.el-tree-node {
  display: table-cell;
  position: relative;
  padding: 20px 5px 0 10px;
}
.el-tree-node:after {
    left: 50% !important;
    border-left: 1px solid #ddd;
}
.el-tree-node__children {
    display: table;
    position: relative;
    padding-top: 20px;
}
.el-tree-node__children:before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 0;
    height: 20px;
    border-left: 1px solid #ddd;
}
.el-tree-node__children:after {
    content: "";
    display: table;
    clear: both;
}
.el-tree-node:after, .el-tree-node:before, .el-tree-node__children:after, .el-tree-node__children:before {
    transition: all .35s;
}
.el-tree-node__content {
  padding: 10px !important;
  display: inline-flex;
  flex-flow: column-reverse;
  border: 1px solid #ddd;
  height: auto;
  text-align: center;
  border-radius: 3px;
  box-shadow: 0 1px 5px rgba(0,0,0,.15);
}
.el-tree-node:after, .el-tree-node:before {
    content: "";
    position: absolute;
    top: 0;
    left: auto;
    width: 50%;
    height: 19px;
}
.el-tree-node:not(:last-child):after, .el-tree-node:not(:last-child):before {
    border-top: 1px solid #ddd;
}
.el-tree > .el-tree-node:after {
    border-left: 0;
    border-top: 0;
}
.el-tree > .el-tree-node:before {
    border:0;
}
</style>